@charset "utf-8";

.todo-item {padding: 1rem; display: flex; align-items: center; cursor: pointer; transition: 0.5s; user-select: none; background-color: #f8f8f8;}
.todo-tiem:hover {background-color: #ccc;}
.todo-item:hover .remove {opacity: 1;}
.todo-item + .todo-item {background-color: #f8f8f8;}
.remove {margin-right: 1rem; color: #e64980; font-weight: 600; opacity: 0;}
.todo-text {flex: 1; word-break: break-all;}
.todo-text > div {background-color: #f8f8f8}
.checked {text-decoration: line-through; color: #ff8fb4;}
.check-mark {font-size: 1.5rem; line-height: 100%; margin-left: 1rem; color: #ff8fb4; font-weight: bold; background-color: #f8f8f8;}