@charset "utf-8";

.body {width: 600px; height: 600px; margin: 0 auto; background-color: #fff; border-radius: 20px; box-shadow: 0 3px 3px rgba(0,0,0,0.2); position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%);}
.todo-list-template {width: 520px; margin: 0 auto;}

.title {padding: 1rem; font-size: 2.1rem; text-align: center; font-weight: bold; color: #ff8fb4; line-height: 100px;}

.form-wrapper {padding: 1rem; border-bottom: 2px dashed #ff8fb4;}

.todos-wrapper {margin-top: 20px; min-height: 5rem;}