@charset "utf-8";

.form {display: flex; margin-bottom: 20px;}
.form input {flex: 1; font-size: 1.25rem; outline: none; border: 1px solid rgb(204, 204, 204, 0.6); box-shadow: rgba(0, 0, 0, 0.1) inset 0 0 5px; border-radius: 10px; background-color: #fff;}
.create-button {padding: 0.5rem 1rem; margin-left: 1rem; background-color: #ff8fb4; border-radius: 3px; color: #fff; font-weight: bold; cursor: pointer;}
.create-button:hover {background-color: #ffd7e4;}